<template>
  <div class="index">
  	<router-link to="/asd">to asd</router-link>
    <h1>{{ msg }}</h1>
    <h1>asd: {{ asd }}</h1>
    <div class="aaa">
    	<ul>
    		<li>{{$store.state.index.someMsg}}</li>
    		<div @click="changeStoreMsg"> 点我改变store的信息</div>
    	</ul>
    </div>
  </div>
</template>

<script>
export default {
  name: 'index',
  data () {
    return {
    	myTitle:"index-title",
      msg: 'Welcome to Your Vue.js App',
      asd:""
    }
  },
  metaInfo: {
    title: "index",
    titleTemplate: '%s - by vue-meta index',
    meta:[
    	{name: 'viewport ', content: 'initial-scale=1.0,user-scalable=no,maximum-scale=1,width=device-width'}
    ],
    htmlAttrs: {
      lang: 'zh'
    },
//  script: [{innerHTML: 'console.log("hello index!")', type: 'text/javascript'}],
//  __dangerouslyDisableSanitizers: ['script']
  },
  created:function(){
  	let _this = this;
		this.$http.get("./static/asd.json",{asdasdasdasda:123}).then(function(res){_this.asd = res.data.a})
  },
  methods:{
  	changeStoreMsg:function(){
  		this.$store.commit("changeIndexMsg",++this.$store.state.index.someMsg)
  	}
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
.aaa{
	ul{
		font-size:0.24rem;
		padding-bottom: 0.55rem;
		li{color: #ff0000;}
	}
}
</style>



